.music-card{
    background-color: var(--ac-gray);
    padding: 1rem;
    display: inline-block;
    width: 380px;
    -webkit-filter: drop-shadow(8px 8px 5px var(--ac-primary));
            filter: drop-shadow(8px 8px 5px var(--ac-primary));
    margin: 10px;
    -webkit-transition: -webkit-filter 350ms ease-in;
    transition: -webkit-filter 350ms ease-in;
    transition: filter 350ms ease-in;
    transition: filter 350ms ease-in, -webkit-filter 350ms ease-in;
}

.music-card:hover{
    -webkit-filter: drop-shadow(0px 0px 100px var(--ac-white));
            filter: drop-shadow(0px 0px 100px var(--ac-white));

}

#content{
    width: 85%;
    margin: auto;
    margin-bottom: 20px;
}

.grid-container{
    width: 75%;
    margin: auto;
}

.thumb{
    width: 100%;
    margin: auto;
}

.caption{
    text-decoration: none;
    color: var(--ac-text-gray);
    text-align: left;
}

.design-title{
    font-size: 3rem;
    font-weight: 500;
    margin-bottom: 15px;
}

@media only screen and (max-width: 1250px){
    .music-card{
        width: 90%;
        float: none;
        margin: 20px auto;
        -webkit-filter: drop-shadow(4px 4px 5px var(--ac-primary));
                filter: drop-shadow(4px 4px 5px var(--ac-primary));

    }
    .grid-container{
        width: 95%;
    }
    .music-card:hover{
        -webkit-filter: drop-shadow(4px 4px 5px var(--ac-primary));
                filter: drop-shadow(4px 4px 5px var(--ac-primary));
        -webkit-transition: none;
        transition: none;

    }
}
